import Header from '../../containers/header';
import FooterTop from '../../containers/FooterTop';
import kivi from '../../assets/images/icons/kivi.svg';


import { Link } from 'react-router-dom';

import './RegistrationOne.scss'

const RegistrationThree = () => {
  return (
    <>
      <Header />

      <div className="registration">
        <div className="container">
          <div className="row d-flex align-items-center justify-content-center">
            <div className="col-lg-4">
              <div className="left">
                <h4 className="left-title second">Бесплатная регистрация</h4>

                <form action="" className="third-form">
                  <p className="top-text">Код отправлен в виде смс на указанный номер телефона</p>


                  <label htmlFor="num">Введите ваш номер телефона</label>
                  <div className="row">
                    <div className="col-lg-9">
                      <input type="tel" name="phone-number" id="numb" className="inp-form" placeholder="998 97 772 13 44"/>
                    </div>
                    <div className="col-lg-3">
                      <div className="seconds-block">90s</div>
                    </div>
                  </div>

                  <input type="text" name="code" id="code" className="inp-form code" placeholder="Введите код подтверждения"/>

                  <Link to="/registrationFour" className="submit-btn">Далее</Link>
                </form>

                <div className="left-bottom">
                  <span className="yoki">или</span>

                  <Link className="vxod-taklif">Войдите в систему, если у вас уже есть аккаунт kivi.uz</Link>
                </div>

              </div>
            </div>
            <div className="col-lg-6">
              <div className="right">

                <div className="open-for-you">
                  <img src={kivi} alt="" className="open-for-you-img" />
                  <p className="open-for-you-text">
                    Откройте для себя
                    целую плошадку
                    для продаже
                  </p>
                  <Link className="btn btn-outline-success">Как это делать?</Link>
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>

      <FooterTop />
    </>
  )
}

export default RegistrationThree;