import {
  BrowserRouter as Router,
  Switch,
  Route
} from 'react-router-dom';

import './assets/styles/main.scss';

import Home from './pages/Home';
import SinglePage from './pages/SinglePage';
import SearchPage from './pages/SearchPage';
import { RegistrationOne, RegistrationTwo, RegistrationThree, RegistrationFour } from './pages/RegistrationOne';
import Profile from './pages/Profile';
import Filters from './pages/Filters';

function App() {

  return (
    <Router>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route path="/SinglePage" component={SinglePage} />
        <Route path="/searchPage" component={SearchPage} />
        <Route path="/registration" component={RegistrationOne} />
        <Route path="/registrationTwo" component={RegistrationTwo} />
        <Route path="/registrationThree" component={RegistrationThree} />
        <Route path="/registrationFour" component={RegistrationFour} />
        <Route path="/profile" component={Profile} />
        <Route path="/filters" component={Filters} />
      </Switch>

    </Router>
  );
}

export default App;
