import './Main.scss';
import MainCard from '../../components/MainCard';

import { CardElements } from '../../components/CardElements';

const Main = () => {
  return (
    <>
      <div className="main">
        <div className="container">
          <div className="row d-flex justify-content-between">

            {
              CardElements.map((item) => (
                <div className="col-lg-3">
                  <MainCard
                    goTo="/SinglePage"
                    image={item.image}
                    text={item.title}
                    time={item.time}
                    price={item.price}
                  />
                </div>
              ))
            }

          </div>
        </div>
      </div>
    </>
  )
}

export default Main;