import Header from '../../containers/header';
import { Link } from 'react-router-dom';
import { useState } from 'react';

import './Profile.scss';

import LittleCard2 from '../../components/LittleCard2';

import Airplane from '../../assets/images/icons/Airplane.svg';
import Store from '../../assets/images/icons/Store.svg';
import Terminal from '../../assets/images/icons/Terminal.svg';
import Briefcase from '../../assets/images/icons/Briefcase.svg';
import Gifts from '../../assets/images/icons/Gifts.svg';
import Challenge from '../../assets/images/icons/Challenge.svg';
import Maintenance from '../../assets/images/icons/Maintenance.svg';
import Gaming from '../../assets/images/icons/Gaming.svg';
import Man from '../../assets/images/icons/Man.svg';

import SwiperCore, { Navigation, Pagination } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';

import 'swiper/swiper.scss';
import 'swiper/components/navigation/navigation.scss';

SwiperCore.use([Navigation, Pagination]);

const Profile = () => {

  const [rubricheskiyMenu, setRubricheskiyMenu] = useState(false);

  return (
    <div className="profile-first">

      <div className={`overlay-wrapper ${rubricheskiyMenu ? 'active' : ''}`}></div>

      <div className="overlay"></div>
      <Header />

      <div className="container">

        <div className="profile-first-div">
          <h3 className="profile-first-title">Добавить обьявления бесплатно</h3>

          <p className="profile-first-subtitle">Для добавления вашего обьявлении вы должны выбрать категорию</p>
        </div>



        <div className="top-block mb-3">
          <Swiper
            slidesPerView={9}
            navigation
            pagination={{ clickable: true }}
            className="d-flex justify-content-between"
          >

            <SwiperSlide>
              <LittleCard2
                image={Airplane}
                title="Автомобили"
                clas="little-card first"
                rubricheskiyMenu={rubricheskiyMenu}
                setRubricheskiyMenu={setRubricheskiyMenu}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard2
                image={Store}
                title="Недвижимость"
                clas="little-card second"
                rubricheskiyMenu={rubricheskiyMenu}
                setRubricheskiyMenu={setRubricheskiyMenu}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard2
                image={Terminal}
                title=" Электроника"
                clas="little-card third"
                rubricheskiyMenu={rubricheskiyMenu}
                setRubricheskiyMenu={setRubricheskiyMenu}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard2
                image={Briefcase}
                title="Работа"
                clas="little-card fourth"
                rubricheskiyMenu={rubricheskiyMenu}
                setRubricheskiyMenu={setRubricheskiyMenu}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard2
                image={Gifts}
                title="Личные вещи"
                clas="little-card fifth"
                rubricheskiyMenu={rubricheskiyMenu}
                setRubricheskiyMenu={setRubricheskiyMenu}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard2
                image={Challenge}
                title="Дом и Сад"
                clas="little-card second"
                rubricheskiyMenu={rubricheskiyMenu}
                setRubricheskiyMenu={setRubricheskiyMenu}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard2
                image={Maintenance}
                title="Стройка и ремонт"
                clas="little-card first"
                rubricheskiyMenu={rubricheskiyMenu}
                setRubricheskiyMenu={setRubricheskiyMenu}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard2
                image={Gaming}
                title="Хобби и Спорт"
                clas="little-card fifth"
                rubricheskiyMenu={rubricheskiyMenu}
                setRubricheskiyMenu={setRubricheskiyMenu}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard2
                image={Man}
                title="Бизнес и услуги"
                clas="little-card third"
                rubricheskiyMenu={rubricheskiyMenu}
                setRubricheskiyMenu={setRubricheskiyMenu}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard2
                image={Challenge}
                title="Дом и Сад"
                clas="little-card second"
                rubricheskiyMenu={rubricheskiyMenu}
                setRubricheskiyMenu={setRubricheskiyMenu}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard2
                image={Maintenance}
                title="Стройка и ремонт"
                clas="little-card first"
                rubricheskiyMenu={rubricheskiyMenu}
                setRubricheskiyMenu={setRubricheskiyMenu}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard2
                image={Gaming}
                title="Хобби и Спорт"
                clas="little-card fifth"
                rubricheskiyMenu={rubricheskiyMenu}
                setRubricheskiyMenu={setRubricheskiyMenu}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard2
                image={Man}
                title="Бизнес и услуги"
                clas="little-card third"
                rubricheskiyMenu={rubricheskiyMenu}
                setRubricheskiyMenu={setRubricheskiyMenu}
              />
            </SwiperSlide>
          </Swiper>
        </div>

        <div className={`rubricheskiy-menu ${rubricheskiyMenu ? 'active' : ''}`}>
          <div className="row">
            <div className="col-lg-4">
              <h3 className="rubricheskiy-menu-title">Рубрика</h3>
              <ul>
                <li><Link className="rubricheskiy-menu-link">Детский мир</Link></li>
                <li><Link className="rubricheskiy-menu-link active">Недвижимость</Link></li>
                <li><Link className="rubricheskiy-menu-link">Транспорт</Link></li>
                <li><Link className="rubricheskiy-menu-link">Работа</Link></li>
                <li><Link className="rubricheskiy-menu-link">Животные</Link></li>
                <li><Link className="rubricheskiy-menu-link">Дом и сад</Link></li>
                <li><Link className="rubricheskiy-menu-link">Электроника</Link></li>
                <li><Link className="rubricheskiy-menu-link">Бизнес и услуги</Link></li>
                <li><Link className="rubricheskiy-menu-link">Мода и стиль</Link></li>
                <li><Link className="rubricheskiy-menu-link">Хобби, отдых и спорт</Link></li>
              </ul>
            </div>

            <div className="col-lg-4">
              <h3 className="rubricheskiy-menu-title">Недвижимость</h3>
              <ul>
                <li><Link className="rubricheskiy-menu-link">Квартиры</Link></li>
                <li><Link className="rubricheskiy-menu-link active">Дома</Link></li>
                <li><Link className="rubricheskiy-menu-link">Земля</Link></li>
                <li><Link className="rubricheskiy-menu-link">Гаражи / стоянки</Link></li>
                <li><Link className="rubricheskiy-menu-link">Коммерческие помещения</Link></li>
              </ul>
            </div>

            <div className="col-lg-4">
              <h3 className="rubricheskiy-menu-title">Дома</h3>
              <ul className="bez-border">
                <li><Link to="/filters" className="rubricheskiy-menu-link">Аренда долгосрочная</Link></li>
                <li><Link to="/filters" className="rubricheskiy-menu-link">Аренда посуточно</Link></li>
                <li><Link to="/filters" className="rubricheskiy-menu-link">Продажа</Link></li>
                <li><Link to="/filters" className="rubricheskiy-menu-link">Обмен</Link></li>
              </ul>
            </div>
          </div>
        </div>


      </div>
    </div>
  )
}

export default Profile;