import { Link, Switch, Route, useRouteMatch } from 'react-router-dom';
import './Menu.scss';

import NestedMenu from './NestedMenu';

const Menu = ({ mainMenuState }) => {

  const { path, url } = useRouteMatch();


  console.log(path, url)
  return (
    <Switch>
      <Route exact path={`${path}`}>
        <div className={`main-menu ${mainMenuState ? 'active' : ''}`}>
          <div className="row">
            <div className="col-lg-3">
              <ul>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Телефоны </Link></li>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Компьютеры </Link></li>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Фото / видео </Link></li>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Тв / видеотехника </Link></li>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Аудиотехника </Link></li>
              </ul>
            </div>

            <div className="col-lg-3">
              <ul>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Игры и игровые приставки </Link></li>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Техника для дома </Link></li>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Техника для кухни </Link></li>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Климатическое оборудование </Link></li>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Индивидуальный уход </Link></li>
              </ul>
            </div>

            <div className="col-lg-3">
              <ul>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Аксессуары и комплектующие</Link></li>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Прочая электроника </Link></li>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Телефоны </Link></li>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Компьютеры </Link></li>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Фото / видео </Link></li>
              </ul>
            </div>

            <div className="col-lg-3">
              <ul>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Телефоны </Link></li>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Компьютеры </Link></li>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Фото / видео </Link></li>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Тв / видеотехника </Link></li>
                <li className="main-menu-link"><Link to={`${url}/nestedMenu`}> Аудиотехника </Link></li>
              </ul>
            </div>
          </div>
        </div>
      </Route>

      <Route path={`${url}/nestedMenu`} component={NestedMenu}></Route>
    </Switch>
  )
}

export default Menu;