import {Link} from 'react-router-dom';

import './LittleCard.scss';

const LittleCard = ({ image, title, clas, setMainMenuState, mainMenuState}) => {
  return (
    <Link className="little" onClick={() => setMainMenuState(!mainMenuState)}>
      <button className={clas}>
        <img src={image} alt="" />
      </button>
      <h3 className="title">{title}</h3>
    </Link>
  )
}

export default LittleCard;