import { Link } from 'react-router-dom';
import './AddCard.scss';

const AddCard = ({ clas, text, howMuch }) => {
  return (
    <Link className={clas}>
      <h3 className="text">{text}</h3>
      <span className="how-much">{howMuch} обьявлениий</span>
    </Link>
  )
}

export default AddCard;