import './Home.scss';

import Header from '../../containers/header';
import Main from '../../containers/Main';
import Bottom from '../../containers/BottomBlock';
import FooterTop from '../../containers/FooterTop';
import FooterBottom from '../../containers/FooterBottom';
import { Menu } from '../../containers/Menu';

import LittleCard from '../../components/LittleCard';
import AddCard from '../../components/AddCard';

import Airplane from '../../assets/images/icons/Airplane.svg';
import Store from '../../assets/images/icons/Store.svg';
import Terminal from '../../assets/images/icons/Terminal.svg';
import Briefcase from '../../assets/images/icons/Briefcase.svg';
import Gifts from '../../assets/images/icons/Gifts.svg';
import Challenge from '../../assets/images/icons/Challenge.svg';
import Maintenance from '../../assets/images/icons/Maintenance.svg';
import Gaming from '../../assets/images/icons/Gaming.svg';
import Man from '../../assets/images/icons/Man.svg';

import { Link } from 'react-router-dom';
import { useState } from 'react';

import SwiperCore, { Navigation, Pagination } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';

import 'swiper/swiper.scss';
import 'swiper/components/navigation/navigation.scss';

SwiperCore.use([Navigation, Pagination]);

const Home = () => {

  const [mainMenuState, setMainMenuState] = useState(false);

  return (
    <>
      <div className={`layout-wrapper ${mainMenuState ? 'active' : ''}`}></div>

      <Header />

      <div className="container mb-5">

        <div className="top-block mb-3">
          <Swiper
            slidesPerView={9}
            navigation
            pagination={{ clickable: true }}
            className="d-flex justify-content-between"
          >

            <SwiperSlide>
              <LittleCard
                image={Airplane}
                title="Автомобили"
                clas="little-card first"
                setMainMenuState={setMainMenuState}
                mainMenuState={mainMenuState}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard
                image={Store}
                title="Недвижимость"
                clas="little-card second"
                setMainMenuState={setMainMenuState}
                mainMenuState={mainMenuState}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard
                image={Terminal}
                title=" Электроника"
                clas="little-card third"
                setMainMenuState={setMainMenuState}
                mainMenuState={mainMenuState}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard
                image={Briefcase}
                title="Работа"
                clas="little-card fourth"
                setMainMenuState={setMainMenuState}
                mainMenuState={mainMenuState}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard
                image={Gifts}
                title="Личные вещи"
                clas="little-card fifth"
                setMainMenuState={setMainMenuState}
                mainMenuState={mainMenuState}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard
                image={Challenge}
                title="Дом и Сад"
                clas="little-card second"
                setMainMenuState={setMainMenuState}
                mainMenuState={mainMenuState}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard
                image={Maintenance}
                title="Стройка и ремонт"
                clas="little-card first"
                setMainMenuState={setMainMenuState}
                mainMenuState={mainMenuState}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard
                image={Gaming}
                title="Хобби и Спорт"
                clas="little-card fifth"
                setMainMenuState={setMainMenuState}
                mainMenuState={mainMenuState}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard
                image={Man}
                title="Бизнес и услуги"
                clas="little-card third"
                setMainMenuState={setMainMenuState}
                mainMenuState={mainMenuState}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard
                image={Challenge}
                title="Дом и Сад"
                clas="little-card second"
                setMainMenuState={setMainMenuState}
                mainMenuState={mainMenuState}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard
                image={Maintenance}
                title="Стройка и ремонт"
                clas="little-card first"
                setMainMenuState={setMainMenuState}
                mainMenuState={mainMenuState}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard
                image={Gaming}
                title="Хобби и Спорт"
                clas="little-card fifth"
                setMainMenuState={setMainMenuState}
                mainMenuState={mainMenuState}
              />
            </SwiperSlide>

            <SwiperSlide>
              <LittleCard
                image={Man}
                title="Бизнес и услуги"
                clas="little-card third"
                setMainMenuState={setMainMenuState}
                mainMenuState={mainMenuState}
              />
            </SwiperSlide>

          </Swiper>
        </div>

        <Menu mainMenuState={mainMenuState}/>

        <div className="main-block">
          <div className="main-block-middle">
            <div className="block-menu">
              <Link className="block-menu-link active">Новые объявления</Link>
              <Link className="block-menu-link">Лучщие предложение</Link>
            </div>

            <Main />

          </div>

          <div className="main-block-bottom">
            <Link className="main-block-bottom-link">Покозать еще</Link>
          </div>

        </div>


      </div>

      <div className="add-block mb-3">
        <div className="container">
          <Swiper
            spaceBetween={29}
            slidesPerView={3}
            className="d-flex justify-content-between"
          >
            <SwiperSlide>
              <AddCard
                clas="add-card"
                text="Техника Apple по доступным ценам"
                howMuch="987"
              />
            </SwiperSlide>

            <SwiperSlide>
              <AddCard
                clas="add-card second"
                text="Брендовые товары за копейки"
                howMuch="987"
              />
            </SwiperSlide>

            <SwiperSlide>
              <AddCard
                clas="add-card third"
                text="Все товары для офиса в одном месте"
                howMuch="987"
              />
            </SwiperSlide>

            <SwiperSlide>
              <AddCard
                clas="add-card"
                text="Техника Apple по доступным ценам"
                howMuch="987"
              />
            </SwiperSlide>

            <SwiperSlide>
              <AddCard
                clas="add-card second"
                text="Брендовые товары за копейки"
                howMuch="987"
              />
            </SwiperSlide>

            <SwiperSlide>
              <AddCard
                clas="add-card third"
                text="Все товары для офиса в одном месте"
                howMuch="987"
              />
            </SwiperSlide>

          </Swiper>
        </div>
      </div>

      <div className="bottom-block">
        <div className="container">
          <div className="bottom-block-middle">
            <div className="block-menu">
              <Link className="block-menu-link active">Просмотренные</Link>
              <Link className="block-menu-link">Выбор редакции</Link>
              <Link className="block-menu-link">Сниженные цены</Link>
            </div>
            <Bottom />
          </div>
          <div className="main-block-bottom">
            <Link className="main-block-bottom-link">Покозать еще</Link>
          </div>
        </div>
      </div>

      <footer>
        <FooterTop />
        <FooterBottom />
      </footer>

    </>
  )
}

export default Home;