import { Link } from 'react-router-dom';

import './LittleCard2.scss';

const LittleCard2 = ({ image, title, clas, rubricheskiyMenu, setRubricheskiyMenu }) => {
  return (
    <Link className="little" onClick={() => setRubricheskiyMenu(!rubricheskiyMenu)}>
      <button className={clas}>
        <img src={image} alt="" />
      </button>
      <h3 className="title">{title}</h3>
    </Link>
  )
}

export default LittleCard2;