import Header from '../../containers/header';
import FooterTop from '../../containers/FooterTop';
import kivi from '../../assets/images/icons/kivi.svg';


import { Link } from 'react-router-dom';

import './RegistrationOne.scss'

const RegistrationFour = () => {
  return (
    <>
      <Header />

      <div className="registration">
        <div className="container">
          <div className="row d-flex align-items-center justify-content-center">
            <div className="col-lg-4">
              <div className="left">
                <h4 className="left-title second">Ваша регистрация завершена!</h4>

                <form action="" className="fourth-form">
                  <p className="top-text">Вы успешно зарегистрированы на сайте киви и ваше пароль отправлен в виде смс на ваше телефонный номер</p>

                  <label htmlFor="user">Введите email или номер телефона</label>
                  <input type="tel" name="e-mail ili tel numb" id="user" className="inp-form" placeholder="99891 167 27 23" />

                  <label htmlFor="parol">Пароль</label>
                  <input type="password" name="password" id="parol" className="inp-form" placeholder="***********"/>

                  <Link to="#" className="submit-btn">Далее</Link>
                </form>

                <div className="left-bottom">
                  <span className="yoki">или</span>

                  <Link className="vxod-taklif">Войдите в систему, если у вас уже есть аккаунт kivi.uz</Link>
                </div>

              </div>
            </div>
            <div className="col-lg-6">
              <div className="right">

                <div className="open-for-you">
                  <img src={kivi} alt="" className="open-for-you-img" />
                  <p className="open-for-you-text">
                    Откройте для себя
                    целую плошадку
                    для продаже
                  </p>
                  <Link className="btn btn-outline-success">Как это делать?</Link>
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>

      <FooterTop />
    </>
  )
}

export default RegistrationFour;