import './SinglePage.scss';

import { Link } from 'react-router-dom';

import Ovoshi from '../../assets/images/ovoshi.jpg';
import Diod from '../../assets/images/diod.jpg';
import Kotedj from '../../assets/images/kotedj.jpg';
import kv2 from '../../assets/images/kv2.jpg';
import Krovat from '../../assets/images/krovat.jpg';
import Home from '../../assets/images/home.jpg';

import MainCard from '../../components/MainCard';
import { CardElements3 } from '../../components/CardElements';

import Header from '../../containers/header';
import FooterTop from '../../containers/FooterTop';
import FooterBottom from '../../containers/FooterBottom';


import SwiperCore, { Navigation, Pagination } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';

import 'swiper/swiper.scss';
import 'swiper/components/navigation/navigation.scss';

SwiperCore.use([Navigation, Pagination]);

const SinglePage = () => {
  return (
    <>

    <Header />

      <div className="container">
        <span className="breadcrumb">Объявления   /   Ташкент Недвижимость   /   Ташкент Квартиры   /   Ташкент Продажа   /   Новостройки Ташкент</span>

        <h1 className="title">Срочно Голден Хаус Етти Чинор Новостройка 4х ком на 1 этаже юнусабад 5</h1>

        <div className="row">
          <div className="col-lg-6">
            <div className="row">
              <div className="col-lg-3">
                <img src={Ovoshi} alt="" className="mini-img active" />
                <img src={Diod} alt="" className="mini-img" />
                <img src={Kotedj} alt="" className="mini-img" />
                <img src={kv2} alt="" className="mini-img" />
                <img src={Krovat} alt="" className="mini-img" />
              </div>
              <div className="col-lg-7">
                <img src={Home} alt="" className="big-img" />
              </div>
            </div>
          </div>

          <div className="col-lg-6">
            <div className="right">
              <div className="right-top">
                <span className="right-top-span">Артикул: 4418970</span>
                <span className="right-top-span">обновлено: 28.04.17 добавлено: 28.04.17</span>
              </div>

              <h3 className="right-price">70 000 у.е.</h3>

              <h3 className="right-location">Ташкент, Ташкентская область, Юнусабадский район</h3>

              <div className="right-number">
                <div className="right-number-link col-6">
                  <p className="right-number-link-text">99891 166 ****</p>
                  <button className="right-number-link-button">Покозать польностю</button>
                </div>

                <div className="right-number-link col-6">
                  <p className="right-number-link-text">99891 166 ****</p>
                  <button className="right-number-link-button">Покозать польностю</button>
                </div>
              </div>

              <div className="author">
                <p className="author-text">Автор обьявлении: </p>
                <Link className="author-name">Зухриддин Темиров</Link>
              </div>

              <div className="right-btns">
                <Link className="right-btns-btn btn btn-primary">Написать автору</Link>
                <Link className="right-btns-btn btn btn-purple">Предложить свою цену</Link>
              </div>

              <div className="about">
                <div className="row">

                  <div className="col-lg-4 about-card">
                    <h5 className="title">Количество комнат:</h5>
                    <p className="info">6</p>
                  </div>

                  <div className="col-lg-4 about-card">
                    <h5 className="title">Общая площадь:</h5>
                    <p className="info">40 м2</p>
                  </div>

                  <div className="col-lg-4 about-card">
                    <h5 className="title">Этаж:</h5>
                    <p className="info">32</p>
                  </div>

                  <div className="col-lg-4 about-card">
                    <h5 className="title">Этажность дома:</h5>
                    <p className="info">6</p>
                  </div>

                  <div className="col-lg-4 about-card">
                    <h5 className="title">Состояние квартиры:</h5>
                    <p className="info">Первая сдача</p>
                  </div>

                  <div className="col-lg-4 about-card">
                    <h5 className="title">Этаж:</h5>
                    <p className="info">32</p>
                  </div>

                  <div className="col-lg-4 about-card">
                    <h5 className="title">Планировка</h5>
                    <p className="info blue">Раздельная</p>
                  </div>

                  <div className="col-lg-4 about-card">
                    <h5 className="title">Год постройки/сдачи</h5>
                    <p className="info blue">Сдача в 2017</p>
                  </div>

                  <div className="col-lg-4 about-card">
                    <h5 className="title">Ремонт</h5>
                    <p className="info">Авторский проект</p>
                  </div>

                  <div className="col-lg-4 about-card">
                    <h5 className="title">Санузел</h5>
                    <p className="info blue">2 санузла и более</p>
                  </div>

                  <div className="col-lg-4 about-card">
                    <h5 className="title">Меблирована</h5>
                    <p className="info blue">Да</p>
                  </div>

                  <div className="col-lg-4 about-card">
                    <h5 className="title">Высота потолков</h5>
                    <p className="info">32</p>
                  </div>

                  <div className="col-lg-12 about-card">
                    <h5 className="title">Рядом есть</h5>
                    <p className="info blue">Больница, поликлиника, Детская, площадка, Детский сад, Остановки, Парк, зелёная зона, Развлекательные заведения, Рестораны кафе</p>
                  </div>

                </div>
              </div>

              <p className="text-secondary fw-bold mb-4">
                Срочно продаётся 3х ком на 1 этоже 7 этажном кирпичном доме. Новостройка Голден Хаус жылой комплекс Етти Чинор Очень качественный и дорогой ремонт. Комнаты полнастю разделные болшые. 2 сан узла. Для лишних вещей есть кладовка.Ремонт шыкарный обсалютно новый.
              </p>

              <p className="text-secondary fw-bold mb-4">
                Ремонт делолся из очень дорогова материяла. Дорогая класическая кухонная мебель. Джаккузи,Тёплые полы в каждой комнате. Дорогие люстры вовсех комнатах решодки на окнах. Теретория охранается. Всё предусмотренно для детей деские площядки качели итд. Торг наместе. Торопитесь продаю нужны денги. Первый золотой этаж и паследний в этом коплексе. Торопитесь.
              </p>

              <div className="right-bottom d-flex align-items-center">
                <p className="right-bottom-left">Просмотры:10958</p>
                <p className="right-bottom-right text-danger">Пожаловатся</p>
              </div>

            </div>
          </div>
        </div>

        <div className="bottom">

          <div className="block-menu">
            <Link className="block-menu-link active">Похожие объявления</Link>
            <Link className="block-menu-link">Обьявление автора</Link>
          </div>

          <div className="bottom-middle">

            <Swiper
              spaceBetween={25}
              slidesPerView={4}
              navigation
              pagination={{ clickable: true }}
              className="d-flex justify-content-between"
            >
              {
                CardElements3.map((item) => (
                  <div className="col-lg-3">
                    <SwiperSlide>
                      <MainCard
                        goTo="/SinglePage"
                        image={item.image}
                        text={item.title}
                        time={item.time}
                        price={item.price}
                      />
                    </SwiperSlide>
                  </div>
                ))
              }

            </Swiper>

          </div>
        </div>
      </div>

      <FooterTop />
      <FooterBottom />
    </>
  )
}

export default SinglePage;