import './header.scss';
import Logo from '../../assets/images/icons/logo.svg';

import { Link } from 'react-router-dom';


const Header = () => {
  return (
    <div className="header mb-3">

      <div className="container">
        <div className="header-top">

          <Link to="/" className="left">
            <img src={Logo} alt="" />
            <h3 className="title">Продай, найди, купи все что пожелаешь…</h3>
          </Link>

          <div className="menu">
            <Link to="/" className="menu__link">Объявления</Link>
            <Link to="/" className="menu__link">Магазины</Link>
            <Link to="/" className="menu__link">Для бизнеса</Link>
            <Link to="/" className="menu__link">Помощь</Link>
          </div>

          <div className="right">
            <div className="language">
              <Link to="/" className="lang marked">Рус</Link>
              <Link to="/" className="lang">O’z</Link>
            </div>

            <div className="register">
              <Link className="registration">Вход</Link>
              <Link to="/registration" className="registration">Регистрация</Link>
            </div>
          </div>

        </div>

        <div className="bottom">
          <div className="left">
            <form action="https://echo.htmlacademy.ru">
              <select class="form-select header-select" name="categories" aria-label="Default select example">
                <option selected>Любая категория</option>
                <option value="1">One</option>
                <option value="2">Two</option>
                <option value="3">Three</option>
              </select>

              <input class="form-control header-select" type="search" name="what" placeholder="Что будем искать ?" aria-label="Search"></input>

              <select class="form-select header-select" name="location" aria-label="Default select example">
                <option selected>По всей Узбекистану</option>
                <option value="1">One</option>
                <option value="2">Two</option>
                <option value="3">Three</option>
              </select>

              <button type="submit">Найти</button>
            </form>
          </div>

          <Link to="/" className="bottom-right">
            Добавить обьявления
          </Link>
        </div>

      </div>
    </div>
  )
}

export default Header;