import Header from '../../containers/header';
import FooterTop from '../../containers/FooterTop';
import kivi from '../../assets/images/icons/kivi.svg';
import arr from '../../assets/images/icons/arrow-down.svg';

import { Link } from 'react-router-dom';

import './RegistrationOne.scss'

const RegistrationTwo = () => {
  return (
    <>
      <Header />

      <div className="registration">
        <div className="container">
          <div className="row d-flex align-items-center justify-content-center">
            <div className="col-lg-4">
              <div className="left">
                <h4 className="left-title">Бесплатная регистрация</h4>

                <form action="" className="second-form">
                  <label htmlFor="email">Введите ваш email</label>
                  <input type="email" name="e-mail" id="email" className="inp-form" placeholder="example@kivi.uz" />

                  <label htmlFor="numb">Введите ваш номер телефона</label>
                  <div className="row">
                    <div className="col-lg-4">
                      <input type="tel" name="phone-number" id="numb" className="inp-form" placeholder="998" />
                    </div>
                    <div className="col-lg-8">
                      <input type="tel" name="phone-number" id="numb" className="inp-form" placeholder="97 772 13 44" />
                    </div>
                  </div>

                  <p className="left-text">
                    <span className="text-primary">Нажимая на кнопку «Получить доступ», я даю согласие на обработку персональных данных и соглашаюсь c условиями</span> договора-оферты и политикой конфиденциальности
                  </p>

                  <div className="row">
                    <div className="col-lg-4">
                      <Link to="/registration" className="back-link"><img src={arr} alt="" /></Link>
                    </div>
                    <div className="col-lg-8">
                      <Link to="registrationThree" className="submit-btn">Получить доступ</Link>
                    </div>
                  </div>

                </form>

                <div className="left-bottom">
                  <span className="yoki">или</span>

                  <Link className="vxod-taklif">Войдите в систему, если у вас уже есть аккаунт kivi.uz</Link>
                </div>

              </div>
            </div>
            <div className="col-lg-6">
              <div className="right">

                <div className="open-for-you">
                  <img src={kivi} alt="" className="open-for-you-img" />
                  <p className="open-for-you-text">
                    Откройте для себя
                    целую плошадку
                    для продаже
                  </p>
                  <Link className="btn btn-outline-success">Как это делать?</Link>
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>

      <FooterTop />
    </>
  )
}

export default RegistrationTwo;