import Logo from '../../assets/images/icons/logo2.svg';
import AppStore from '../../assets/images/icons/AppStore.svg';
import GooglePlay from '../../assets/images/icons/GooglePlay.svg';

import { Link } from 'react-router-dom';

import './FooterTop.scss';

const FooterTop = () => {
  return (
    <div className="footer-top">
      <div className="container">

        <div className="logo">
          <img src={Logo} alt="" />
          <h3 className="logo-title">Продай, найди, купи все что ты пожелаешь</h3>
        </div>

        <div className="center">
          <h3 className="center-text">Веб сайт бесплатных обьявлений | на базе имеется 234 944 999 шт обяления</h3>
        </div>

        <div className="right">
          <Link className="me-1">
            <img src={AppStore} alt="" />
          </Link>

          <Link>
            <img src={GooglePlay} alt="" />
          </Link>
        </div>

      </div>
    </div>
  )
}

export default FooterTop;