import { CardElements2 } from '../../components/CardElements';
import MainCard from '../../components/MainCard';

const Bottom = () => {
  return (
    <div className="row">
      {
        CardElements2.map((item) => (
          <div className="col-lg-3">
            <MainCard
              goTo="/SinglePage"
              image={item.image}
              text={item.title}
              time={item.time}
              price={item.price}
            />
          </div>
        ))
      }
    </div>
  )
}

export default Bottom;